body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0;
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
}

header {
  width: 100%;
  background-color: #8B0000;
  padding: 10px 0;
}

.header-content {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
}

.top-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #8B0000; /* Dark red background */
}

.bottom-row {
  padding: 10px;
  background-color: #8B0000; /* Dark red background */
}

.logo-container {
  display: flex;
  align-items: center;
}

.header-logo {
  width: 50px; /* Adjust size as needed */
  height: auto;
  margin-right: 10px;
}

.header-title {
  color: #FFD700; /* Gold color for TARAJI+ text */
  font-size: 24px;
  font-weight: bold;
}

.nav-container {
  display: flex;
  align-items: center;
  gap: 20px;
}

.main-nav {
  display: flex;
  gap: 20px;
}

.main-nav a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
  font-size: 18px;
}

.main-nav a.active {
  font-weight: bold;
  border-bottom: 2px solid #FFD700;
}

.filter-buttons {
  display: flex;
  justify-content: center;
}

.filter-buttons button {
  margin: 0 10px;
  padding: 5px 10px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
}

.filter-buttons button.active {
  background-color: #FFD700; /* Gold color for active button */
  color: #8B0000;
}

.featured-article {
  margin-bottom: 1rem;
}

.featured-article img {
  width: 100%;
  height: 400px;
  object-fit: cover;
  object-position: center;
}

.next-match {
  background-color: white;
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.next-match h3 {
  margin-top: 0;
}

.next-match .match-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.next-match .match-info img {
  width: 50px;
  height: 50px;
  object-fit: contain;
}

footer {
  background-color: #8B0000;
  color: #FFD700;
  padding: 1rem;
  margin-top: 1rem;
  text-align: center;
}

footer nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 0;
}

footer nav ul li {
  margin: 0 10px;
}

footer nav ul li a {
  color: #FFD700;
  text-decoration: none;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    align-items: flex-start;
  }

  header nav ul {
    margin-top: 10px;
  }

  header nav ul li {
    margin-left: 0;
    margin-right: 20px;
  }
}

/* Add these styles */
.article-list article a {
  text-decoration: none;
  color: inherit;
  display: block;
  height: 100%;
}

.article-list article:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.home-page {
  width: 100%;
  background-color: #f0f0f0;
}

.home-content {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.featured-article,
.next-match {
  max-width: 1200px;
  margin: 0 auto 20px;
  padding: 0 20px;
}

@media (max-width: 1200px) {
  .featured-article,
  .next-match {
    padding: 0 10px;
  }
}

.load-more-button {
  display: block;
  width: 200px;
  margin: 20px auto;
  padding: 10px 20px;
  background-color: #8B0000;
  color: #FFD700;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.load-more-button:hover {
  background-color: #660000;
}

.load-more-button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.loading-indicator {
  text-align: center;
  padding: 20px;
  font-size: 18px;
  color: #8B0000;
}

/* You can add a spinning animation for the loading indicator if desired */
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-indicator::after {
  content: '';
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-left: 10px;
  border: 2px solid #8B0000;
  border-top: 2px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.filter-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.filter-buttons button {
  margin: 0 10px;
  padding: 5px 10px;
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;
  font-size: 16px;
}

.filter-buttons button.active {
  background-color: #FFD700; /* Gold color for active button */
  color: #8B0000;
}

.link-unstyled {
  color: inherit;
  text-decoration: none;
}